:root {
  --proxima: "Proxima Nova";
  --white: #fff;
  --black: #313131;
  --blue: #028A91;
  --blue-2: #13B9C2;
  --blue-3: #73F8F1;
  --green: #124734;
  --green-2: #1D7754;
  --green-3: #86E3A6;
  --yellow: #FF9700;
  --yellow-2: #FFB140;
  --yellow-3: #FFC673;
  --radius: 30px;
}

.button {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  line-height: 1;
  max-width: 340px;
  color: var(--white);
  border-radius: 30px;
  transition: 0.3s ease all;
}

.mobile-only {
  display: none !important;
}

.title {
  font-size: 48px;
  color: var(--black);
  font-weight: 700;
  text-transform: uppercase;
}

@media (max-width: 767px) {
  .desktop-only {
    display: none !important;
  }
  .mobile-only {
    display: flex !important;
  }
  .title {
    font-size: 32px;
    color: var(--black);
    font-weight: 700;
    text-transform: uppercase;
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.header__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  border-radius: 40px;
  padding: 20px 7px 20px 20px;
}
.header__logo {
  flex: 0 1 340px;
}
.header__nav {
  flex: 0 1 30%;
  margin-right: auto;
}
.header__list {
  display: flex;
  align-items: center;
  gap: 55px;
}
.header__list_item {
  position: relative;
}
.header__list_item a {
  transition: 0.3s ease all;
}
.header__list_item::after {
  content: "";
  position: absolute;
  top: calc(50% - 2.5px);
  left: -15px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
}
.header__list_item:nth-child(1):hover a {
  color: var(--blue-2);
}
.header__list_item:nth-child(2):hover a {
  color: var(--yellow-2);
}
.header__list_item:nth-child(3):hover a {
  color: var(--green-3);
}
.header__list_item:nth-child(1)::after {
  background-color: var(--blue-2);
}
.header__list_item:nth-child(2)::after {
  background-color: var(--yellow-2);
}
.header__list_item:nth-child(3)::after {
  background-color: var(--green-3);
}
.header__languages {
  flex: 0 1 13%;
  display: flex;
  align-items: center;
  gap: 10px;
}
.header__languages a {
  transition: 0.3s ease all;
}
.header__languages a:hover {
  color: var(--green-2);
}
.header__languages a:active {
  font-weight: 700;
}
.header__sign {
  border: 1px solid var(--black);
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius);
  padding: 0px 40px;
  transition: 0.3s ease all;
}
.header__sign:hover {
  color: var(--white);
  background-color: var(--yellow-2);
  border-color: var(--yellow-2);
}
.header__sign:active {
  background-color: var(--yellow);
}

@media (max-width: 767px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
  }
  .header__box {
    display: flex;
    align-items: center;
    justify-content: inherit;
    background-color: white;
    border-radius: 0px 0px 40px 40px;
    padding: 15px 16px;
    margin: 0px -15px 0px -15px;
  }
  .header__logo {
    flex: 1 1 auto;
    max-width: 88px;
    z-index: 1;
  }
  .header__logo img {
    width: 100%;
  }
  .header__nav {
    flex: 0 1 30%;
    margin-right: inherit;
    position: fixed;
    top: 0;
    left: -200%;
    height: 100%;
    width: 100%;
    background-color: var(--white);
    display: flex;
    transition: 0.3s ease all;
  }
  .header__nav.active {
    left: 0;
  }
  .header__button_write {
    background-color: var(--green-2);
    margin-top: 90px;
  }
  .header__button_pay {
    background-color: var(--blue-2);
  }
  .header__list {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    gap: 24px;
    padding: 0px 16px;
  }
  .header__list_item {
    position: relative;
  }
  .header__list_item a {
    transition: 0.3s ease all;
    font-size: 32px;
    color: var(--green-2);
    font-weight: 700;
  }
  .header__list_item::after {
    display: none;
  }
  .header__list_item:nth-child(1):hover a {
    color: var(--blue-2);
  }
  .header__list_item:nth-child(2):hover a {
    color: var(--yellow-2);
  }
  .header__list_item:nth-child(3):hover a {
    color: var(--green-3);
  }
  .header__list_item:nth-child(1)::after {
    background-color: var(--blue-2);
  }
  .header__list_item:nth-child(2)::after {
    background-color: var(--yellow-2);
  }
  .header__list_item:nth-child(3)::after {
    background-color: var(--green-3);
  }
  .header__languages {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
    margin-left: auto;
    justify-content: flex-end;
    margin-right: 32px;
    z-index: 1;
  }
  .header__languages a {
    transition: 0.3s ease all;
  }
  .header__languages a:hover {
    color: var(--green-2);
  }
  .header__languages a:active {
    font-weight: 700;
  }
  .header__burger {
    height: 24px;
    width: 24px;
    position: relative;
    cursor: pointer;
  }
  .header__burger::before, .header__burger::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 18px;
    background-color: var(--black);
    transition: 0.3s ease all;
  }
  .header__burger::before {
    top: 6px;
    right: 3px;
  }
  .header__burger::after {
    top: 14px;
    right: 3px;
  }
  .header__burger span::before, .header__burger span::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 14px;
    background-color: var(--black);
    transition: 0.3s ease all;
  }
  .header__burger span::before {
    top: 10px;
    right: 3px;
  }
  .header__burger span::after {
    top: 18px;
    right: 3px;
  }
  .header__burger.active::before, .header__burger.active::after {
    top: 12px;
    right: 6px;
  }
  .header__burger.active::before {
    transform: rotate(45deg);
  }
  .header__burger.active::after {
    transform: rotate(-45deg);
  }
  .header__burger.active span::before, .header__burger.active span::after {
    transform: scale(0);
  }
  .header__sign {
    font-size: 16px;
    align-items: center;
  }
}
